<template>
    <v-container fluid class="container pa-0">
        <play-program-exercise
            :motionCamera_playZone="motionCamera_playZone"
            v-on:close-on="ResetPlayItems()"
        />
        <common-confirm-dialog
            :dialog="reconnetDialog"
            :dialogItems="dialogItems"
            v-on:close-cancel="CancelDialog()"
            v-on:close-allow="AllowDialog()"
        />
    </v-container>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex'
import common from '@/mixins/common.js'
import exercise from '@/mixins/exercise.js'
import timer_program from '@/mixins/timer_program.js'
import motionCamera from '@/mixins/motionCamera.js'

export default {
    name: 'ProgramPlay',
    mixins:[ exercise, timer_program, common, motionCamera],
    data:()=>({
        dialogItems:{
            title: null,
            text: null,
            close: '취소',
            confirm: '확인'
        },
        reconnetDialog:false
    }),
    computed:{
        ...mapGetters([
            'GET_MENU_SELECT_EXERCISE',
            'GET_MENU_SELECT_PROGRAM',
            'GET_PROGRAM_PLAY_EXERCISE_MODE',
            'GET_PROGRAM_PLAY_TOTAL_SET',
            'GET_PROGRAM_PLAY_PLAY_SET',
            'GET_PROGRAM_PLAY_EXERCISE_TIME',
            'GET_PROGRAM_PLAY_PLAY_ITEMS',
            'GET_PROGRAM_PLAY_CURRENT_PART',
            'GET_PROGRAM_PLAY_TOTAL_EXERCISE_TIME',
            'GET_PROGRAM_PLAY_GOAL_COUNT',
            'GET_PROGRAM_PLAY_REST_SET',
            'GET_PROGRAM_PLAY_START_SET',
        ]),
        
    },
    watch:{
        GET_PROGRAM_PLAY_PLAY_SET(value){
            if(value > 1){
                this.RecodeDetail()
                this.PushResultData()
                if(value > this.GET_PROGRAM_PLAY_TOTAL_SET){
                    this.timer_program_stop()
                    this.MU_PROGRAM_PLAY_EXERCISE(false)
                    this.PushCompleteData()
                }
            }
        },
        GET_PROGRAM_PLAY_CURRENT_PART(value){
            if(value > 0) this.EditDetailData()
        },
        motionCamera_exercise_count(){
            this.MU_PROGRAM_PLAY_PLAY_ITEMS_EXERCISE_COUNT(this.GET_PROGRAM_PLAY_PLAY_ITEMS.exerciseCount + 1)
            // motion part 운동 종료
            if(this.GET_PROGRAM_PLAY_PLAY_ITEMS.exerciseCount === this.GET_PROGRAM_PLAY_GOAL_COUNT){
                this.MU_PROGRAM_PLAY_PLAY_ITEMS_EXERCISE_COUNT(0)
                this.MU_PROGRAM_PLAY_EXERCISE(false)
                if(this.GET_PROGRAM_PLAY_EXERCISE_MODE === 2){
                    this.motionCamera_requestToStopRecognition()
                    this.motionCamera_requestToDisconnect()
                }
            }
        },
        motionCamera_dialog(value){
            if(value !== null){
                this.statusStop = true
                this.motionCamera_requestToDisconnect()
                this.dialogItems = {
                    title: value.title,
                    text: value.message,
                    close: '운동감지 취소',
                    confirm: '웹소켓 연결 재시도'
                }
                this.reconnetDialog = true
            }
        },
        motionCamera_webSocket_play(value){
            if(!value){
                this.MU_PROGRAM_PLAY_EXERCISE_MODE(1)
            }
        }
    },
    created() {
    },
    mounted() {
    },
    destroyed(){
    },
    methods: {
        ...mapActions([
            // Streching
            'ACT_PROGRAM_PLAY_API_SET_PROGRAM_STRETCH_RESULT',
            'ACT_PROGRAM_PLAY_API_SET_PROGRAM_STRETCH_COMPLETE',

            // Program
            'ACT_PROGRAM_PLAY_API_SET_PROGRAM_RESULT',
            'ACT_PROGRAM_PLAY_API_SET_PROGRAM_COMPLETE',
        ]),
        ...mapMutations([
            // Exercise Mode
            'MU_PROGRAM_PLAY_EXERCISE_MODE',
            'MU_PROGRAM_PLAY_EXERCISE',
            'MU_PROGRAM_PLAY_PAUSE',

            // Exercise
            'MU_PROGRAM_PLAY_EXERCISE_TIME',
            'MU_PROGRAM_PLAY_START_SET',
            'MU_PROGRAM_PLAY_START_TIME',
            'MU_PROGRAM_PLAY_REST_TIME',
            'MU_PROGRAM_PLAY_REST_SET',
            'MU_PROGRAM_PLAY_TOTAL_EXERCISE_TIME',
            'MU_PROGRAM_PLAY_TOTAL_REST_TIME',
            'MU_PROGRAM_PLAY_TOTAL_SET',
            'MU_PROGRAM_PLAY_PLAY_SET',
            'MU_PROGRAM_PLAY_TOTAL_PART',
            'MU_PROGRAM_PLAY_CURRENT_PART',
            'MU_PROGRAM_PLAY_GOAL_COUNT',
            'MU_PROGRAM_PLAY_PLAY_ITEMS',
            'MU_PROGRAM_PLAY_PLAY_ITEMS_EXERCISE_COUNT',
            'MU_PROGRAM_PLAY_PLAY_ITEMS_EXERCISE_SET',
            'MU_PROGRAM_PLAY_PLAY_ITEMS_DETAIL_ITMES',

            // Report
            'MU_REPORT_EXERCISE_TIME',
            'MU_REPORT_RESULT_DETAIL',
            'MU_REPORT_ITEMS',
        ]),
        RecodeDetail(){
            // exerciseSet + 1
            this.MU_PROGRAM_PLAY_PLAY_ITEMS_EXERCISE_SET(this.GET_PROGRAM_PLAY_PLAY_ITEMS.exerciseSet + 1)
            
            const time = this.common_time_string(this.GET_PROGRAM_PLAY_TOTAL_EXERCISE_TIME-1)
            const recode = `${this.GET_PROGRAM_PLAY_PLAY_SET-1}세트 | ${time}`
            
            let detailItems = {
                title: this.GET_MENU_SELECT_EXERCISE.name,
                success: true,
                recode: recode
            }
            
            this.MU_REPORT_EXERCISE_TIME(this.GET_PROGRAM_PLAY_EXERCISE_TIME)
            this.MU_REPORT_RESULT_DETAIL(detailItems)
            this.MU_REPORT_ITEMS(this.GET_PROGRAM_PLAY_PLAY_ITEMS)

            // exercise time Reset
            this.MU_PROGRAM_PLAY_TOTAL_EXERCISE_TIME(0)
        },
        EditDetailData(){
            const currentPart = this.GET_MENU_SELECT_PROGRAM[this.GET_PROGRAM_PLAY_CURRENT_PART-1] 
            
            if(currentPart){
                // motion 운동 check
                this.CheckMotionMode(currentPart)
                let params = {
                    exSeq           : currentPart.exSeq,
                    subExSortCode   : currentPart.subExSortCode,
                    exTime          : currentPart.exTime,
                    restTime        : this.GET_PROGRAM_PLAY_REST_SET,
                }
                this.MU_PROGRAM_PLAY_PLAY_ITEMS_DETAIL_ITMES(params)
            }
        },
        CheckMotionMode(currentPart){
            let exerciseMode = 1 // Time 모드
            // env 확인
            const envMotionMode = process.env.VUE_APP_MOTION_CARMERA_MODE
            // motion 운동 확인
            const check = this.motionCamera_checkExercise(currentPart.subExSortCode)
            if(envMotionMode === 'true' && check){
                exerciseMode = 2 // motion 모드
                this.MU_PROGRAM_PLAY_GOAL_COUNT(10) // 목표 횟수 등록
                this.motionCamera_open(currentPart.subExSortCode)
            }
            this.MU_PROGRAM_PLAY_EXERCISE_MODE(exerciseMode)
        },
        async PushResultData(){
            let setData = {
                setStartTime: this.GET_PROGRAM_PLAY_PLAY_ITEMS.setStartTime,
                setSeq      : this.GET_PROGRAM_PLAY_PLAY_SET-1,
                subExCount  : this.GET_MENU_SELECT_PROGRAM.lenght,
                exTime      : this.GET_PROGRAM_PLAY_START_SET,
                restTime    : this.GET_PROGRAM_PLAY_REST_SET,
                setDetail   : this.GET_PROGRAM_PLAY_PLAY_ITEMS.detailItems
            }
            await this.ACT_PROGRAM_PLAY_API_SET_PROGRAM_RESULT(setData)
        },
        async PushCompleteData(){
            let params = {
                exSortCode  : this.GET_MENU_SELECT_EXERCISE.exSortCode,
                isPresEx    : 1
            }
            await this.ACT_PROGRAM_PLAY_API_SET_PROGRAM_COMPLETE(params)
            .then(()=>{
                this.$router.push('/user/exercise/report')
            })
        },
        AllowDialog(){
            this.dialogItems    = {}
            this.reconnetDialog = false
            this.EditDetailData()
        },
        CancelDialog(){
            this.dialogItems    = {}
            this.reconnetDialog = false
            this.MU_PROGRAM_PLAY_EXERCISE_MODE(1)
        },
        ResetPlayItems(){
            // WebSocket 종료 예외처리
            if(this.GET_PROGRAM_PLAY_EXERCISE_MODE === 2 || !this.motionCamera_webSocket_play){
                this.motionCamera_requestToStopRecognition()
                this.motionCamera_requestToDisconnect()
            }
            // 초기화
            const exerciseTime     = '00:00'
            const startSet         = 0
            const startTime        = 0
            const restTime         = 0
            const restSet          = 0
            const totalExerciseTime= 0
            const totalRestTime    = 0
            // exercise
            const totalSet    = 0
            const playSet     = 0
            const totalPart   = 0
            const currentPart = 0
            const goalCount   = 0
            const playItems = {
                totalSet     : 0,
                setStartTime : 0,
                exerciseSet  : 0,
                exerciseCount: 0,
                detailItems  : []
            }

            this.MU_PROGRAM_PLAY_EXERCISE(false)
            this.MU_PROGRAM_PLAY_PAUSE(false)
            this.MU_PROGRAM_PLAY_EXERCISE_TIME(exerciseTime)
            this.MU_PROGRAM_PLAY_START_SET(startSet)
            this.MU_PROGRAM_PLAY_START_TIME(startTime)
            this.MU_PROGRAM_PLAY_REST_TIME(restTime)
            this.MU_PROGRAM_PLAY_REST_SET(restSet)
            this.MU_PROGRAM_PLAY_TOTAL_EXERCISE_TIME(totalExerciseTime)
            this.MU_PROGRAM_PLAY_TOTAL_REST_TIME(totalRestTime)

            this.MU_PROGRAM_PLAY_TOTAL_SET(totalSet)
            this.MU_PROGRAM_PLAY_PLAY_SET(playSet)
            this.MU_PROGRAM_PLAY_TOTAL_PART(totalPart)
            this.MU_PROGRAM_PLAY_CURRENT_PART(currentPart)
            this.MU_PROGRAM_PLAY_GOAL_COUNT(goalCount)
            this.MU_PROGRAM_PLAY_PLAY_ITEMS(playItems)
        },
    },
}
</script>

<style lang="scss">
.play_contents{
    height: 100%;
}
</style>